import { VStack, Text } from "@chakra-ui/react";
import MotifBySize from "./MotifBySize";
import { ColorTheme, Stitch, StitchList } from "@tanyoknits/shared";
import { MotifBySizeItem } from "../utils/types";

interface Props {
  intro: string;
  motifBySize: MotifBySizeItem[];
  theme: ColorTheme | undefined;
  stitches: Stitch[];
}
export default function StitchPattern({
  intro,
  motifBySize,
  theme,
  stitches,
}: Props) {
  return (
    <VStack spacing={8} align="left">
      <Text>{intro}</Text>
      <MotifBySize
        motifBySize={motifBySize.map((m) => {
          const { motif, sizes } = m;
          return { size: sizes.join(", "), motif };
        })}
        theme={theme}
      />
      <StitchList stitches={stitches} />
    </VStack>
  );
}
