import {
  Abbr,
  Techniques,
  MotifInPattern,
  definition,
  techniques,
  getVerticalStackedChart,
} from "@tanyoknits/shared";
import { getPyramidMotif } from "@tanyoknits/shared";
import { MotifBySizeItem, PageInfo } from "../../utils/types";
import { sizes } from "./pattern";
import profile from "./profile";
import StitchPattern from "../../components/StitchPattern";

// Stitches in charts
const { k, c2l, c2r, m1l, lli, llip } = definition;

// Charts
const stacked = [
  getPyramidMotif(5, true),
  getPyramidMotif(4, true),
  getPyramidMotif(3, true),
  getPyramidMotif(2, true),
];

const motif1: MotifInPattern = {
  stsCount: stacked[1].stsCount,
  chart: getVerticalStackedChart(stacked.slice(1, 4)),
  glyphSize: "M",
};
const motif2: MotifInPattern = {
  stsCount: stacked[0].stsCount,
  chart: getVerticalStackedChart(stacked.slice(0, 3)),
  glyphSize: "M",
};

const sizeCutID = 6;
export const motifABySize: MotifBySizeItem[] = [
  // Add untitled title since they are an original motif
  {
    sizes: sizes.slice(0, sizeCutID),
    motif: { ...stacked[3], title: undefined },
  },
  { sizes: sizes.slice(sizeCutID), motif: { ...stacked[2], title: undefined } },
];

export const motifBBySize: MotifBySizeItem[] = [
  { sizes: sizes.slice(0, sizeCutID), motif: motif1 },
  { sizes: sizes.slice(sizeCutID), motif: motif2 },
];

