import { Select, VStack, Text, Flex, Alert, AlertIcon } from "@chakra-ui/react";
import {
  Direction,
  MotifComponent,
  MotifInPattern,
  ColorTheme,
} from "@tanyoknits/shared";
import { useState } from "react";

interface Props {
  motifBySize: { size: string; motif: MotifInPattern; direction?: Direction }[];
  theme?: ColorTheme;
}

export default function MotifBySize({ motifBySize, theme }: Props) {
  const [selectedSize, setSelectedSize] = useState<number>(0);
  const { motif, direction } = motifBySize[selectedSize];
  return (
    <VStack spacing={8} align="left">
      <Flex
        gap={4}
        align={["left", "center"]}
        flexDirection={["column", "row"]}
      >
        {motifBySize.length > 1 && (
          <>
            <Text variant="bold">Select size</Text>
            <Select
              onChange={(e) => setSelectedSize(+e.target.value)}
              width="fit-content"
              defaultValue={selectedSize}
              mr={[0, 8]}
            >
              {motifBySize.map((motif, i) => (
                <option key={i} value={i}>
                  {motif.size}
                </option>
              ))}
            </Select>
          </>
        )}
        <Alert status="info" width="fit-content" colorScheme={theme}>
          <AlertIcon />
          Chart is interactive - click a stitch to see the corresponding written
          instruction.
        </Alert>
      </Flex>
      <MotifComponent {...motif} direction={direction} theme={theme} />
    </VStack>
  );
}
